import React, { Component } from 'react';
import { combineReducers, createStore, applyMiddleware, compose } from 'redux';
import createSagaMiddleware from 'redux-saga';
import { select, put, takeEvery } from 'redux-saga/effects';
import { Map, List, Set } from 'immutable';
import shuffle from 'shuffle-array';
import { createSelector } from 'reselect';
import { connect, Provider } from 'react-redux';
import FlipMove from 'react-flip-move';
import logo from './logo.svg';
import keys from './keys.png';
import './App.css';

import stringify from 'csv-stringify/lib/sync';
import parse from 'csv-parse/lib/sync';

// do i need to import thte css
import { FormGroup, Label, Input, Form, Collapse, Button, ButtonGroup, Progress, Card, CardBody, CardText} from 'reactstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

import { namesString } from './Names.js';
import { MdKeyboardArrowDown, MdKeyboardArrowUp, MdKeyboardArrowLeft, MdKeyboardArrowRight } from 'react-icons/md';


function mod(n,m) {
    return ((n % m) + m) % m;
}

// State is: list of names (which needs to be shuffled)
// List of annotations (m/f/u)
//
// List of names in the order they are to be displayed
// A mapping of name to gender (if for some reason the name is in there twice, this will label it automatically...)
// Index of list

const getNames = state => state.namesAndIndex.names;
const getIndex = state => state.namesAndIndex.index;
const getLabels = state => state.labels;
const getAppState = state => state.appState;
const getUserName = state => state.userName;

const get3NamesAndLabels = createSelector(
    [ getNames, getIndex, getLabels ],
    ( names, index, labels ) => {
        if (index === null) return [];
        
        // Just do 1...
        return names.slice(index === 0 ? 0 : index - 1 /*was1*/, index + 2 /*was4*/).map(name => {
            console.log(name);
            console.log(labels.toArray());
            return {
                name,
                label: labels.get(name, ""),
                isMain: name === names.get(index)
            }
        }).toJS();
    }
);

const getCSV = createSelector(
    [ getLabels ],
    ( labels ) => {
        return stringify(labels.toArray(), {
            header: true,
            columns: ["name","gender"]
        });
    }
);

// Show tutorial/examples at start and ask for username
// Label until the end, which on final label goes to FINISH_LABELLING
// Present download button asking to give the file to nello
const appStateReducer = (state = 0, action) => {
    switch (action.type) {
        case "BEGIN_LABELLING":
            return 1;
        case "FINISH_LABELLING":
            return 2;
        default:
            return state;
    }
};

const userNameReducer = (state = "", action) => {
    switch (action.type) {
        case "SET_USER_NAME":
            return action.payload.userName;
        default:
            return state;
    }
};

const nameReducer = (state = { names: List(), index: null }, action) => {
    switch (action.type) {
        case "SET_NAMES":
            return {
                names: action.payload.names,
                index: action.payload.names.count() === 0 ? null : 0 // reset the index when the names change
            };
        case "NEXT_INDEX":
            return {
                ...state,
                index: Math.min(state.index + 1, state.names.count())
            }
        case "PREV_INDEX":
            return {
                ...state,
                index: Math.max(state.index - 1, 0)
            }
        default:
            return state;
    }
};

const labelReducer = (state = Map(), action) => {
    switch (action.type) {
        case "SET_NAME_LABEL":
            return state.set(action.payload.name, action.payload.label);
        default:
            return state;
    }
};

const sagaMiddleware = createSagaMiddleware();

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(
    combineReducers({
        namesAndIndex: nameReducer,
        labels: labelReducer,
        userName: userNameReducer,
        appState: appStateReducer
    }), 
    composeEnhancers(applyMiddleware(sagaMiddleware))
);


function* shuffleTask(action) {
    let names = yield select(getNames);
    yield put({
        type: "SET_NAMES",
        payload: {
            names: List(shuffle(names.toJS()))
        }
    });
}

function* nextIndexOrFinishTask(action) {
    let index = yield select(getIndex);
    let names = yield select(getNames);
    if (names.count() - 1 === index) {
        yield put({
            type: "FINISH_LABELLING",
            payload: {}
        });
    } else {
        yield put({
            type: "NEXT_INDEX",
            payload: {}
        });
    };
}

function* downloadCSVTask(action) {
    let userName = yield select(getUserName);
    let csv = yield select(getCSV);

    var filename = "labels-export.txt";

    var blob = new Blob(["# " + userName + "\n# CSV BEGIN\n" + csv], { type: 'text/csv;charset=utf-8;' });
    if (navigator.msSaveBlob) { // IE 10+
        navigator.msSaveBlob(blob, filename);
    } else {
        var link = document.createElement("a");
        if (link.download !== undefined) { // feature detection
            // Browsers that support HTML5 download attribute
            var url = URL.createObjectURL(blob);
            link.setAttribute("href", url);
            link.setAttribute("download", filename);
            link.style.visibility = 'hidden';
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
        }
    }

}

// All actions which query state or produce a side effect
function* rootSaga() {
    yield takeEvery("SHUFFLE_NAMES", shuffleTask);
    yield takeEvery("NEXT_INDEX_OR_FINISH", nextIndexOrFinishTask);
    yield takeEvery("DOWNLOAD_CSV", downloadCSVTask);
};

sagaMiddleware.run(rootSaga);

// One component that renders       name
//                            Male Other Female 

const Row = ({ name, label, isMain, m, o, f}) => {
    // Ensures the height is preserved
    const fixedName = name.length === 0 ? " " : name;
    return (
        <div>
        { isMain ? <h1 style={{ color: "white", whiteSpace: "pre" }}>{fixedName}</h1>
                 : <h3 style={{ color: "grey", whiteSpace: "pre" }}>{fixedName}</h3>
        }
        <ButtonGroup size="lg">
            <Button color={label === "m" ? "success" : "secondary"} disabled={!isMain} onClick={m}>Male</Button>
            <Button color={label === "u" ? "success" : "secondary"} disabled={!isMain} onClick={o}>Neither</Button>
            <Button color={label === "f" ? "success" : "secondary"} disabled={!isMain} onClick={f}>Female</Button>
        </ButtonGroup>

        </div>
    );
};

// Another that renders 3 of the above, and the use of arrow keys dispatches actions which changes the selection and moves the index
// Up goes to prev index, down sets to u and next index, left sets to m and next index, right sets to f and next index

var Col = class extends Component {
    constructor (props) {
        super(props);

        this.handleKey = this.handleKey.bind(this);
    }

    handleKey (e) {
        const row = this.props.rows.find(({ isMain }) => isMain);

        switch (e.keyCode) {
            case 37: // left
                // this is a hack, should have access to the current name more easily/generally
                this.props.onLeft(row.name);
                break;
            case 39: // right
                this.props.onRight(row.name);
                break;
            case 38: // up
                this.props.onUp();
                break;
            case 40: // down
                this.props.onDown(row.name);
                break;
            default:
                break;
        }
    }

    componentDidMount () {
        document.addEventListener("keydown", this.handleKey, false);

        // Initialise state
        store.dispatch({
            type: "SET_NAMES",
            payload: {
                names: List(Set(parse(namesString).map(v => v[0]))).slice(0, 30)
            }
        });

        store.dispatch({
            type: "SHUFFLE_NAMES",
            payload: {}
        });
    }

    componentWillUnmount () {
        document.removeEventListener("keydown", this.handleKey, false);
    }

    render () {
        const rows = this.props.rows;
        return (
            <div>
                <Progress value={this.props.progress} />
                <FlipMove maintainContainerHeight={false} duration={50} enterAnimation={"elevator"} leaveAnimation={"elevator"} disableAllAnimations={true}>
                    {rows.map(({ name, label, isMain}) => {
                        if (isMain) {
                            return (
                                <div key={name} className="mt-4 mb-4">
                                    <Card body inverse color="info">
                                        <CardBody>
                                            <CardText className="mb-0">What do you think is the gender of this user?<br/>(use neither for organizations, or uncertain cases)</CardText>
                                            <Row isMain={true} name={name} label={label} m={() => this.props.onLeft(name)} o={() => this.props.onDown(name)} f={() => this.props.onRight(name)}/>
                                        </CardBody>
                                    </Card>
                                </div>
                            );
                        } else { 
                            return (
                                <div key={name} className="mt-4 mb-4">
                                    <div>
                                        <Row isMain={false} name={name} label={label} />
                                    </div>
                                </div>
                            );
                        }
                    })}
                </FlipMove>
                <Button onClick={this.props.onUp} color="danger" className="mr-2">{"Go back one"}</Button>
                <Button onClick={this.props.done} color="success">{"I'm done"}</Button>
                <p className="mt-4">Use the MOUSE or ARROW KEYS:<br/><img src={keys} width="100"/><br/><MdKeyboardArrowUp/>Go Back One<br/><MdKeyboardArrowLeft/> Male<br/><MdKeyboardArrowRight/> Female<br/><MdKeyboardArrowDown/> Neither</p>
            </div>
        );
    }
};

Col = connect(state => {
    return {
        rows: get3NamesAndLabels(state),
        progress: getIndex(state)/(getNames(state).count()-1)*100
    };
}, dispatch => {
    return {
        onLeft: name => {
            dispatch({ type: "SET_NAME_LABEL", payload: { name, label: "m" } });
            dispatch({ type: "NEXT_INDEX_OR_FINISH", payload: {}});
        },
        onRight: name => {
            dispatch({ type: "SET_NAME_LABEL", payload: { name, label: "f" } });
            dispatch({ type: "NEXT_INDEX_OR_FINISH", payload: {}});
        },
        onDown: name => {
            dispatch({ type: "SET_NAME_LABEL", payload: { name, label: "u" } });
            dispatch({ type: "NEXT_INDEX_OR_FINISH", payload: {}});
        },
        onUp: () => {
            dispatch({ type: "PREV_INDEX", payload: {}});
        },
        done: () => {
            dispatch({ type: "FINISH_LABELLING", payload: {} })
        },
    };
})(Col);

var Intro = ({ userName, changeUserName, next }) => {
    return (
        <div>
            <h1>Gender Survey - the latest!</h1>
            <p>The following survey will ask you whether a name represents a male or female person.</p>
            <p>Some names will be ambiguous or represent non-individuals (an organisation, for example) - in these cases please mark as "neither"</p>
            <Form onSubmit={next}>
                <FormGroup>
                    <Input 
                        type="text"
                        placeholder="Your name"
                        value={userName} 
                        onChange={({target: {value}}) => changeUserName(value)}
                    />
                </FormGroup>
            </Form>
            <Button onClick={next}>Next</Button>
        </div>
    );
};

Intro = connect(state => {
    return {
        userName: getUserName(state)
    };
}, dispatch => {
    return {
        changeUserName: userName => (dispatch({ type: "SET_USER_NAME", payload: { userName} })),
        next: () => (dispatch({ type: "BEGIN_LABELLING", payload: {} })),
    };
})(Intro);

var Download = ({download}) => {
    return (
        <div>
            <h1>THANK YOU!</h1>
            <p>Impressive accuracy!<br/>Before closing this page, please save the following file and send it to Nello</p>
            <Button onClick={download}>Download Results</Button>
        </div>
    );
};

// Surely passing as a prop is terrible for performance?
Download = connect(null, dispatch => {
    return {
        download: () => dispatch({
            type: "DOWNLOAD_CSV",
            payload: {}
        })
    };
})(Download);

var Steps = ({ step }) => {
    switch (step) {
        case 0:
            return (<Intro />);
        case 1:
            return (<Col />);
        case 2:
            return (<Download />);
        default:
            return <div>Error</div>;
    }
};

Steps = connect(state => {
    return {
        step: getAppState(state)
    }
})(Steps);

function App() {
  return (
    <div className="App">
      <header className="App-header">
        <Provider store={store}>
            <Steps />
        </Provider>
      </header>
    </div>
  );
}

export default App;
